.app-wrapper{
  display: grid;
  margin: 0 auto;
  width: 1200px;
  grid-template-areas:
          "h h"
          "n c";
  grid-template-rows: 60px 1fr;
  grid-auto-columns: 2fr 10fr;
  grid-gap: 0px;
}

.app-wrapper-content{
  grid-area: c;
  background-color: chocolate;
  padding: 10px;
}


